<template>
    <div class="collapse-trigger" role="button" @click="accordion($event)">
        <div class="card-header">
            <div class="card-header-title">
                <h2>{{ episode.title }}</h2>
                <a :href="'https://www.podbbang.com/channels/9213/episodes/'+episode.podcast" target="_blank" title=" "  v-if="episode.podcast" class="podcast">
                    <span class="icon is-medium"><i class="mdi mdi-podcast"></i></span>
                </a>
            </div>
            <div class="card-header-icon">
                <span class="icon">
                    <i class="mdi mdi-menu-down mdi-24px"></i>
                </span>
            </div>
        </div>
    </div>
    <div class="collapse-content"  style="display:none">
        <div class="card-content">
            <div class="content">
                <div class="main-link" v-if="episode.resource">
                    <p> </p>
                    <ul class="resource-list">
                        <li v-for="(item, j) in episode.resource" :key="j">
                            <p class="title">{{item.title}}</p>
                            <a :href="item.link" target="_blank" title=" ">{{ item.link }}</a>
                        </li> 
                    </ul>
                    <ul class="keywords-list" v-if="episode.keywords">
                        <li v-for="(keyword, k) in episode.keywords" :key="k" role="button" @click="passEvent($event)">{{ keyword }}</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'episode-item',
    props: {
       episode : Object
    },
    methods: {
        accordion(event) {
            let el = event.currentTarget;
            let target = el.nextElementSibling;
            if(target.style.display == 'none') {
                target.style.display = 'block';
                el.querySelector('.mdi-menu-down').classList.replace('mdi-menu-down', 'mdi-menu-up');
            }else {
                target.style.display = 'none'
                el.querySelector('.mdi-menu-up').classList.replace('mdi-menu-up', 'mdi-menu-down');
            }
        }, 
    }
}
</script>