<template>
<div class="con-inner">
    <div class="top">
        <div class="tit-area">
            <span class="logo sisterhood">
                <span class="blind">    </span>
            </span>
            <div class="tit-txt">
                <h1></h1>
                <p class="author"></p>
            </div>
        </div>
        <season-ctrl :season="season" @sortSeason="sortSeason"/>
    </div>   
    <div class="form-ctrl">
        <div class="ctrl-btn">
            <button type="button" class="button is-primary" @click="sortCtrl">
                <span class="icon is-small"><i class="mdi mdi-sort"></i></span>  
                <span class="tx">{{ sortTx }}</span>  
            </button>
            <button type="button" class="button is-primary" id="btnFilter" @click="setFilterActive">
                <span class="icon is-small"><i class="mdi" v-bind:class="[filterActive ? 'mdi-filter' : 'mdi-filter-outline']"></i></span>  
                <span class="blind"></span>  
            </button>
        </div>
        <div class="input-search">
            <select id="searchOption" @change="setSearchOption($event)">
                <option value="mainTit"> </option>
                <option value="subTit">  </option>
            </select>
            <input type="text" v-model="search">
        </div>
        <p class="notice">*         .</p>
    </div>
    <div class="list">
        <h2 class="blind"> </h2>
         <div class="collapse card" v-for="(episode, idx) in filteredData" :key="idx" :id="'episode' + idx">
             <episode-item :episode="episode"/>
        </div>
    </div>
</div>
</template>

<script src="../../assets/js/sisterhood.js"></script>