<template>
    <div class="graph-text">
        <p class="title">   </p>
        <p class="text">     <span class="m-block">  .</span></p>
    </div>
    <ul class="contact">
        <li class="title">  </li>
        <li>E-mail : <a href="mailto:nicolelim9@gmail.com">nicolelim9@gmail.com</a></li>
        <li>instagram@sheelim17</li>
    </ul>
    <VueForceGraph3D
        ref="graph"
        :graphData="dataSet"
        :nodeThreeObject="setSpriteName"
        :onNodeClick="nodeHandleClick"
        :linkOpacity="0.3"
        :nodeResolution="16"
        :linkColor="setlinkColor"
        :linkWidth="setlinkWidth"
    />
    <button id="btn"  class="btn-mode" @click="setNodeMode"><span v-if="mode == 'node'"> </span><span v-if="mode == 'name'"> </span></button>  
    <div id="layerNodeInfo" class="layer-node-info">
        <p id="nodeName" class="name"></p>
        <p class="link"><a href="" id="nodeLink" target="_blank"></a></p>
        <button class="layer-cls" @click="closeLayer"><i class="mdi mdi-close"></i></button>  
    </div>
</template>


<script>
import listdata from '../../assets/js/network'
import SpriteText from 'three-spritetext';

export default {
    data() {
        return {
            dataSet: {},
            mode: 'node',
            layerActive: false,
        }
    },
    created() {
        this.dataSet = listdata;
    },
    mounted() {

    },
    methods: {
        setNodeMode() {
            if(this.mode == 'node'){
                this.mode = 'name'
            }else {
                this.mode = 'node'
            }
            this.$refs.graph.refresh();
        },
        setlinkColor(link) {
            if(link.color){
                return link.color
            }else {
                return '#fff'
            }
        },
        setlinkWidth(link) {
            if(link.color){
                return 2
            }else {
                return 0
            }
        },
        
        setSpriteName(node) {
            if(this.mode == 'name') {
                const sprite = new SpriteText(node.name);
                sprite.color = node.color;
                return sprite;
            } else {
                if(node.textnode) {
                    const sprite = new SpriteText(node.name);
                    sprite.color = node.color;
                    return sprite;
                }
            }
        
        },
        nodeHandleClick(node) {
            const nodePosition = node.__threeObj.position
            const distance = 100;
            const distRatio = 1 + distance/Math.hypot(nodePosition.x, nodePosition.y, nodePosition.z);
            this.$refs.graph.cameraPosition(
                { x: nodePosition.x * distRatio, y: nodePosition.y * distRatio, z: nodePosition.z * distRatio }, // new position
                nodePosition, // lookAt ({ x, y, z })
                3000  // ms transition duration
            );
            const layer = document.getElementById('layerNodeInfo');
            const name = document.getElementById('nodeName')
            const link = document.getElementById('nodeLink')
            layer.style.display = "none"
            name.innerText = '';
            
            if(node.podcast) {
                name.append(node.name);
                link.setAttribute('href', node.podcast)
                link.innerText = node.podcast;
                setTimeout(function() {
                    layer.style.display = "block"
                }, 3200)
            }else if(node.sns) {
                name.append(node.name);
                link.setAttribute('href', node.snsUrl)
                link.innerText = node.sns;
                setTimeout(function() {
                    layer.style.display = "block"
                }, 3200)
            }  
        },
        closeLayer() {
            const layer = document.getElementById('layerNodeInfo');
            layer.style.display = "none"
        }
    }
}
</script>